"use client";

import { useState } from "react";

export default function MataKuliahList({ initialMataKuliah }) {
  const [mataKuliah, setMataKuliah] = useState(initialMataKuliah);
  const [form, setForm] = useState({
    nama: "",
    dosen: "",
    jadwal: ""
  });

  // Tambah Mata Kuliah
  const handleAdd = (e) => {
    e.preventDefault();

    const newMataKuliah = {
      id: Date.now(), // ID sementara
      ...form
    };

    setMataKuliah([...mataKuliah, newMataKuliah]);
    setForm({ nama: "", dosen: "", jadwal: "" });
  };

  // Hapus Mata Kuliah
  const handleDelete = (id) => {
    setMataKuliah(mataKuliah.filter(m => m.id !== id));
  };

  return (
    <div className="space-y-6">

      {/* FORM TAMBAH MATA KULIAH */}
      <form
        onSubmit={handleAdd}
        className="p-4 bg-gray-100 text-gray-900 rounded-lg shadow space-y-3"
      >
        <h2 className="font-bold text-lg">Tambah Mata Kuliah</h2>

        <input
          type="text"
          placeholder="Nama Mata Kuliah"
          value={form.nama}
          onChange={(e) => setForm({ ...form, nama: e.target.value })}
          className="w-full border p-2 rounded"
          required
        />

        <input
          type="text"
          placeholder="Dosen"
          value={form.dosen}
          onChange={(e) => setForm({ ...form, dosen: e.target.value })}
          className="w-full border p-2 rounded"
          required
        />

        <input
          type="text"
          placeholder="Jadwal (contoh: Senin 08:00-10:00)"
          value={form.jadwal}
          onChange={(e) => setForm({ ...form, jadwal: e.target.value })}
          className="w-full border p-2 rounded"
          required
        />

        <button
          type="submit"
          className="bg-blue-600 text-white px-4 py-2 rounded"
        >
          Tambah
        </button>
      </form>

      {/* DAFTAR MATA KULIAH */}
      <ul className="space-y-3">
        {mataKuliah.map((m) => (
          <li
            key={m.id}
            className="p-4 bg-gray-100 text-gray-900 rounded-lg shadow flex justify-between items-center"
          >
            <div>
              <p className="font-semibold">{m.nama}</p>
              <p className="text-sm text-gray-600">Dosen: {m.dosen}</p>
              <p className="text-sm text-gray-500">Jadwal: {m.jadwal}</p>
            </div>

            <button
              onClick={() => handleDelete(m.id)}
              className="text-red-600 hover:underline"
            >
              Hapus
            </button>
          </li>
        ))}
      </ul>

    </div>
  );
}
